import React from "react";
import classNames from "classnames"
import "./Header.css";

const Header = ({ children, leftButton, rightButton, fixed, color }) => {
  const classes = classNames("Header", {
    "is-fixed":fixed,
    "Header--colorBlue": color === "blue",
    "Header--colorGreen": color === "green",
    "Header--colorRed": color === "red",
    "Header--colorMagenta": color === "magenta",
    "Header--colorBlack": color === "black"
  });

  return (
    <header className={classes}>
      <div className="Header-LeftButton">{leftButton}</div>
      <div className="Header-RightButton">{rightButton}</div>
      <div className="Header-Middle">{children}</div>
    </header>
  );
};
export default Header;
